@import url(https://fonts.googleapis.com/css?family=Quantico);
.sidebar {
  height: 100%;
  position: fixed;
  width: 200px;
  background-color: rgb(26, 25, 25);
  display: inline-block;
  text-align: left;
}

h1 {
  font-size: 35px;
  width: 160px;
  margin: 25% 50px 25% 15px;
  font-family: Quantico;
  background: -webkit-linear-gradient(#59df7d, #64ff8d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

p {
  width: 200px;
  font-family: Quantico;
  color: #64ff8d;
  margin: 15px 15px 0px 17px;

  font-size: 14px;
  opacity: 0.7;
  margin-bottom: 8px;
}

.btn {
  font-family: Quantico;
  font-size: 15px;
  width: 160px;
  height: 50px;
  color: #64ff8d;
  text-align: center;
  display: block;
  border: 1px solid;
  position: relative;
  overflow: hidden;
  margin: 0px 15px 15px 15px;
  background: rgb(26, 25, 25);
}

.btn:before {
  content: "";
  position: absolute;
  height: 100%;
  width: 0;
  background: #64ff8d;
  left: 0;
  bottom: 0;
  transition: all 0.3s;
  opacity: 0.3;
}

.btn:hover:before {
  width: 100%;
}

.btn-active:before {
  width: 100%;
}

.slider {
  -webkit-appearance: none !important;
  width: 150px;
  display: block;
  background: #64ff8d;
  border-radius: 10px;
  transition: all 0.3s ease;
  opacity: 0.7;
  margin-top: 10px;
}

.sliderOutput {
  font-family: Quantico;
  color: #64ff8d;
  font-size: 15px;
  opacity: 0.7;
}

.sliderContainer {
  width: 150px;
  height: 100px;
  margin: 35px 20px;
  text-align: center;
}

.btn-last {
  margin-bottom: 50px;
}

.sliderTitle {
  font-family: Quantico;
  color: #64ff8d;
  font-size: 12px;
  opacity: 0.7;
}

.visualiser {
  position: fixed;
  margin-left: 200px;
  width: 100%;
  height: 100%;
  background-color: rgb(41, 41, 41);
}

.container {
  position: relative;
  margin-top: 15%;
  margin-bottom: 0%;
  margin-left: 5%;
  width: 1000px;
  height: 60%;

  display: flex;
  align-items: flex-end;
}

.bar {
  border: 1px solid #64ff8d;
  /* border-image-source: url(https://wallpaperstock.net/wallpapers/thumbs1/43896wide.jpg);
  border-image-slice: 60 30; */

  display: inline-block;
  width: 0px;
  height: 0px;
  opacity: 0.5;
  margin: 1px;
  transition: height 0.1s ease-out;
}

.bar-trans {
  border: 1px solid #64ff8d;
  transition: height 1s ease-out;
  display: inline-block;
  width: 0px;
  height: 0px;
  opacity: 0.5;
  margin: 1px;
}



body {
  margin: 0;
}

